.project-image {
  width: 90%;
}

.tags > span {
  padding: 0.5rem;
  border-radius: 25%;
  background: white;
  color: black;
  &:nth-child(1) {
    background: rgb(0, 255, 21);
    color: black;
  }
  &:nth-child(2) {
    background: blue;
    color: white;
  }
  &:nth-child(3) {
    background: yellow;
    color: black;
  }
}

.tags::before {
  content: "Tags: ";
  margin-right: 1rem;
  color: black;
}

@media screen and (max-width: 500px) {
  .info {
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;

    & .tags,
    & .updated {
      display: block;
    }
  }
}
