.bg-home::before {
  content: "";
  background: url("../img/bg-main.jpg") no-repeat center center;
  min-height: 105vh;
  background-size: cover;
  filter: blur(5px);
  position: fixed;
  z-index: -99;
  inset: 0;
  -webkit-animation: kenburns-top 5s ease-out both;
  animation: kenburns-top 5s ease-out both;
}

@media screen and (min-width: 960px) {
  #featured-1,
  #featured-2 {
    min-width: 300px;
    width: 40vw;
    margin-right: auto;
  }
}
